/* Full-width background for Statistics section with gradient */
.statsSection {
    width: 100vw;
    background: radial-gradient(ellipse at 60% 40%, #1e293b 0%, #0A1220 70%);
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    position: relative;
    z-index: 1;
}

.statsContainer {
    display: flex;
    justify-content: space-around;
    max-width: 1400px;
    margin: 0 auto;
    padding: 60px 40px;
    background: none;
}

.title {
    color: #fff;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 32px;
    text-align: left;
}

.statItem {
    flex: 1;
    text-align: center;
    padding: 24px 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    margin: 0 10px;
    background: rgba(255, 255, 255, 0.02);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.statItem:hover {
    transform: translateY(-4px);
    border-color: #00D395;
    box-shadow: 0 12px 32px rgba(0, 211, 149, 0.15);
}

.statValue {
    font-size: 2.5rem;
    font-weight: bold;
    color: #00D395;
    margin-bottom: 8px;
}

.statLabel {
    font-size: 1rem;
    color: #B3C5D1;
    font-weight: 500;
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
    .statsContainer {
        flex-direction: column;
        padding: 40px 20px;
        gap: 20px;
    }
    
    .title {
        font-size: 2rem;
        text-align: center;
        margin-bottom: 24px;
    }
    
    .statItem {
        margin: 0;
        padding: 20px 16px;
    }
    
    .statValue {
        font-size: 2rem;
    }
    
    .statLabel {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .statsContainer {
        padding: 30px 16px;
    }
    
    .title {
        font-size: 1.75rem;
    }
    
    .statValue {
        font-size: 1.75rem;
    }
}