.products {
  padding: 100px 0;
  background: #0A1220;
  text-align: center;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

.sectionTitle {
  font-size: 3rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 80px;
  line-height: 1.2;
}

.productsGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-bottom: 60px;
}

.pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
  align-items: center;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.dot.active {
  background: #00D395;
}

/* Mobile Responsive Styles */
@media (max-width: 1024px) {
  .productsGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  
  .container {
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .products {
    padding: 60px 0;
  }
  
  .sectionTitle {
    font-size: 2.5rem;
    margin-bottom: 40px;
  }
  
  .productsGrid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .container {
    padding: 0 16px;
  }
}

@media (max-width: 480px) {
  .products {
    padding: 40px 0;
  }
  
  .sectionTitle {
    font-size: 2rem;
  }
}