.cryptoMarket {
  background: #0A1220;
  padding: 60px 0;
  width: 100%;
}

.marketHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
}

.marketTitle {
  font-size: 2.5rem;
  font-weight: 700;
  color: #fff;
  margin: 0;
}

.viewAllButton {
  background: rgba(255, 255, 255, 0.1);
  color: #9ca3af;
  padding: 12px 20px;
  border-radius: 8px;
  text-decoration: none;
  font-size: 0.95rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.viewAllButton:hover {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
}

.marketGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

.marketColumn {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 32px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.columnHeader {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.columnIcon {
  font-size: 1.2rem;
}

.columnTitle {
  font-size: 1.1rem;
  font-weight: 600;
  color: #fff;
}

/* Mobile Responsive Styles */
@media (max-width: 1024px) {
  .marketGrid {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 0 20px;
  }
  
  .marketHeader {
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .cryptoMarket {
    padding: 40px 0;
  }
  
  .marketHeader {
    flex-direction: column;
    gap: 16px;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .marketTitle {
    font-size: 2rem;
  }
  
  .marketGrid {
    padding: 0 16px;
  }
  
  .marketColumn {
    padding: 24px 20px;
  }
}

@media (max-width: 480px) {
  .marketTitle {
    font-size: 1.75rem;
  }
  
  .marketColumn {
    padding: 20px 16px;
  }
  
  .columnTitle {
    font-size: 1rem;
  }
}